@for $i from 1 to 6 {
	h#{$i},
	.h#{$i} {
		position: relative;
		margin: 0;
		padding: 0;
		font-family: $font-garamond;
		font-weight: 400;

		&.color-white {
			color: $color-white;
		}

		&.color-blue {
			color: $color-endeavour;
		}
	}
}

h1,
.h1 {
	font-size: 120px;
	line-height: 120px;
	
	@media (max-width: 1024px) {
		font-size: 45px;
		line-height: 55px;
	}
}

h2,
.h2 {
	font-size: 85px;
	line-height: 80px;
	
	@media (max-width: 1024px) {
		font-size: 45px;
		line-height: 45px;
	}
}
