.content-area {
	font-size: 18px;
	line-height: 30px;

	p {
		margin-top: 0;
	}

	&__title {
		margin-bottom: 30px;
		color: $color-endeavour;
	}

	&__description {
		margin-bottom: 40px;
		font-size: 24px;
		line-height: 36px;

		@media (max-width: 1024px) {
			margin-bottom: 30px;
		}
	}

	&__description-image {
		margin-top: 5px;
		margin-left: -15px;

		@media (max-width: 1024px) {
			margin-bottom: 40px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__section {
		margin-bottom: 80px;
		
		@media (max-width: 1024px) {
			margin-bottom: 30px;
		}
	}

	ul {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			position: relative;
			margin-bottom: 40px;
			padding-left: 31px;

			@media (max-width: 1024px) {
				margin-bottom: 20px;
			}

			&::before {
				content: "—";
				position: absolute;
				left: 0;
				top: 0;
				display: block;
				font-size: inherit;
				line-height: inherit;
			}
		}
	}
}
