.header {
	position: relative;
	left: 0;
	right: 0;
	top: 0;
	margin-bottom: 20px;
	padding-top: 50px;
	z-index: 100;
	width: 100%;

	&:not(.header_index) {
		&:after {
			content: "";
			position: absolute;
			top: 0;
			right: 100px;
			z-index: -1;
			height: calc(100% + 20px);
			width: 1px;
			background-color: $black;

			@include media-breakpoint-down(md) {
				display: none;
			}
		}

		&.header__open-menu {

			.cart__image {
				background: transparent url("images/navigation/shopping-basket.png") no-repeat center center;
				background-size: contain;
			}

			.cart__count {
				color: $white;
			}

			.header__mobile-menu-btn.active {
				span {
					background-color: $white;
				}

				&:after, &:before {
					background-color: $white;
				}
			}

			&::after {
				opacity: 0;
			}
		}

		.cart__image {
			background: transparent url("images/navigation/shopping-basket-blue.png") no-repeat center center;
			background-size: contain;
		}
	}

	&__nav {
		transition: opacity 0.2s;

		@media (max-width: 1024px) {
			display: none;
		}
	}

	&__mobile-menu-btn {
		position: relative;
		display: none;
		vertical-align: middle;
		margin: 0;
		border: none;
		padding: 0;
		outline: none;
		width: 32px;
		height: 22px;
		background-color: transparent;

		&:hover,
		&:focus,
		&:active {
			outline: none;
		}

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 50%;
			display: block;
			width: 32px;
			height: 4px;
			background-color: $color-black;
			transform-origin: center center;
			transition: top 0.3s, bottom 0.3s, transform 0.3s, background-color .3s ease-out;
		}

		&::before {
			top: 0;
			transform: translateX(-50%);
		}

		&::after {
			top: 100%;
			transform: translate(-50%, -100%);
		}

		span {
			position: absolute;
			left: 50%;
			top: 50%;
			display: block;
			width: 100%;
			height: 4px;
			background-color: $color-black;
			transform: translate(-50%, -50%);
			transition: opacity 0.3s, background-color .3s ease-out;
		}

		&.active {
			&::before,
			&::after {
				top: 50%;
			}

			&::before {
				transform: translate(-50%, -50%) rotate(-45deg);
			}

			&::after {
				transform: translate(-50%, -50%) rotate(45deg);
			}

			span {
				opacity: 0;
			}
		}

		@media (max-width: 1024px) {
			display: inline-block;
			margin-right: 15px;
			margin-left: 38px;
		}

		@media (max-width: 480px) {
			margin-left: 15px;
		}
	}

	&_index {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		z-index: 100;
		margin-bottom: 0;

		.header {
			&__nav-link {
				border-bottom: 1px solid rgba($color-white, 0.5);
				color: $color-white;

				&_sublink {
					border-bottom: 1px dashed rgba($color-white, 0.5);
				}
			}

			&__mobile-menu-btn {
				&::before,
				&::after,
				span {
					background-color: $color-white;
				}
			}
		}
	}

	&.header_index {
		.cart__count {
			color: $white;
		}
	}

	.cart__count {
		color: $black;
	}

	@media (max-width: 1024px) {
		position: fixed;
		padding-top: 30px;
		padding-bottom: 30px;
		transition: background-color 0.3s;

		&.active {
			background-color: $color-endeavour;

			.header {
				&__mobile-menu-btn {
					&::before,
					&::after,
					span {
						background-color: $color-white;
					}
				}
			}
		}
	}
}
