.accordion {
	&__item {
		border-bottom: 1px solid rgba($color-black, 0.17);

		&.active {
			.accordion__title {
				&::before {
					transform: translate(0, -50%) rotate(180deg);
				}
			}
		}
	}

	&__title {
		position: relative;
		padding: 9px 20px 9px 0;
		font-size: 18px;
		line-height: 36px;
		cursor: pointer;

		&::before {
			content: "";
			position: absolute;
			right: 0;
			top: 50%;
			display: block;
			width: 20px;
			height: 20px;
			background: url("../images/4/down-arrow.png") no-repeat center center;
			transform: translate(0, -50%);
			transition: transform 0.2s;
		}
	}

	&__content {
		padding: 15px 0;
	}
}
