.main {
	position: relative;
	margin-bottom: 120px;

	&::after {
		content: "";
		display: block;
		position: absolute;
		right: 100px;
		top: 0;
		height: calc(100% + 120px);
		width: 1px;
		background-color: $black;

		@include media-breakpoint-down(md) {
			display: none;
		}
	}

	@media (max-width: 1024px) {
		padding-top: 120px;
		margin-bottom: 50px;
	}
}
