.main-page-logo {
	position: fixed;
	left: 40px;
	top: 50px;
	z-index: 101;
	display: block;
	width: 82px;
	height: 112px;
	transition: opacity 0.2s;

	@media (max-width: 1024px) {
		left: 25px;
		top: 20px;
		width: 48px;
		height: 50px;
	}

	&__inner {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		transition: opacity 0.2s;

		&_basic {
			opacity: 1;
		}

		&_negative {
			opacity: 0;
		}
	}

	&_reverse {
		.main-page-logo {
			&__inner {
				&_basic {
					opacity: 0;
				}

				&_negative {
					opacity: 1;
				}
			}
		}
	}

	&_hidden {
		opacity: 0;
		pointer-events: none;
	}

	&__desktop {
		@media (max-width: 1024px) {
			display: none;
		}
	}

	&__mobile {
		display: none;
		width: 48px;
		height: 50px;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}

		@media (max-width: 1024px) {
			display: block;
		}
	}

	@media (max-width: 1024px) {
		left: 30px;
	}
}
