.logo-5th {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	text-decoration: none;
	color: $color-white;

	&__icon {
		flex: 0 0 34px;
		margin-right: 15px;
		width: 34px;
		height: 40px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		position: relative;
		font-weight: 300;
		font-size: 16px;
		line-height: 24px;
	}

	&:hover {
		.logo-5th {
			&__text {
				&::before {
					background-color: $color-white;
				}
			}
		}
	}
}
