.section {
	&__container {
		display: flex;
		flex-flow: column nowrap;
		height: 100vh;

		@media (max-width: 1024px) {
			height: auto;
		}

		.section {
			&__title {
				flex: 0 0 auto;
			}

			&__content {
				position: relative;
				flex: 0 1 100vh;
			}
		}
	}

	&__content {
		@media (max-width: 1024px) {
			padding-bottom: 45px;
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		font-weight: 300;
		font-size: 24px;
		line-height: 36px;
		text-decoration: none;

		&_padding {
			padding-left: 35px;

			.fa {
				position: absolute;
				left: 0;
				top: 0;
				line-height: inherit;
			}
		}

		&_pseudo {
			span {
				border-bottom: 1px solid rgba($color-endeavour, 0.3);
				transition: border-bottom-color 0.2s;
			}

			&:hover {
				span {
					border-bottom-color: $color-endeavour;
				}
			}
		}
	}

	&__contacts-item {
		position: relative;
		display: inline-block;
		padding-left: 30px;
		font-size: 18px;
		line-height: 30px;
		
		&_last {
			@media (max-width: 768px) {
				margin-bottom: 30px;
			}
		}

		.fa {
			position: absolute;
			left: 0;
			top: 0;
			line-height: inherit;
			color: $color-endeavour;
		}
	}

	&__link-description {
		font-size: 18px;
	}

	&__blue-text {
		color: $color-endeavour;

		a {
			color: $color-endeavour;

			&:hover,
			&:focus,
			&:visited,
			&:active {
				color: $color-endeavour;
			}
		}
	}

	&__title {
		margin-bottom: 90px;

		@media (max-width: 768px) {
			margin-bottom: 30px;
		}

		&_absolute {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			z-index: 10;
			margin-bottom: 0;
			padding-top: 45px;
		}

		&_blue {
			color: $color-endeavour;
		}
	}

	&__show-all {
		margin-top: 30px;

		@media (max-width: 768px) {
			display: none;
		}
	}

	.footer {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding-bottom: 0;
		height: 30vh;

		@media (max-width: 1024px) {
			position: relative;
			padding-bottom: 40px;
			height: auto;
		}
	}

	&__menu {
		position: relative;
		padding-top: 70px;
		padding-bottom: 160px;
		height: 100%;

		@media (max-width: 1024px) {
			padding-bottom: 0;
		}

		@media (max-width: 768px) {
			padding-top: 0;
			margin-bottom: -45px;
		}
	}

	&__white-bg {
		position: absolute;
		left: -15px;
		right: -15px;
		top: 0;
		bottom: 0;

		@media (max-width: 1024px) {
			left: 0;
			right: 0;
		}

		.container-fluid {
			height: 100%;
		}

		.row {
			height: 100%;
		}

		[class^="col"] {
			height: 100%;
		}
	}

	&__white-bg-content {
		margin-left: -15px;
		margin-right: -15px;
		height: 100%;
		background-color: $color-white;

		@media (max-width: 1024px) {
			margin-left: 0;
			margin-right: 0;
			width: 100%;
		}
	}

	&__mobile-slide {
		@media (max-width: 768px) {
			display: flex;
			flex-flow: row nowrap;
			overflow-x: scroll;

			[class^="col"] {
				flex: 0 0 100%;
			}
		}
	}

	&_align-top {
		.section__title {
			padding-top: 60px;

			@media (max-width: 1024px) {
				padding-top: 30px;
			}

			&_absolute {
				padding-top: 45px;

				@media (max-width: 1024px) {
					padding-top: 30px;
				}
			}
		}

		.fp-tableCell {
			vertical-align: top !important;
		}
	}

	&_blue {
		color: $color-white;
		background-color: $color-endeavour;

		a {
			color: $color-white;

			&:hover,
			&:focus,
			&:visited,
			&:active {
				color: $color-white;
			}
		}

		.section {
			&__link {
				&_pseudo {
					span {
						border-bottom-color: rgba($color-white, 0.3);
					}

					&:hover {
						span {
							border-bottom: 1px solid $color-white;
						}
					}
				}
			}
		}
	}

	&_white {
		background-color: $color-white;
	}

	&_mobile-white {
		@media (max-width: 1024px) {
			background-color: $color-white;

			.color-white {
				color: $color-black;
			}
		}
	}

	&_has-background {
		.section__content {
			&::before {
				content: "";
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				display: block;
				width: 100%;
				max-width: 83.33333%;
				background-color: $color-white;

				@media (max-width: 1024px) {
					max-width: 100%;
				}
			}
		}

		&_reverse {
			.section__content {
				&::before {
					content: "";
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					display: block;
					background-color: $color-white;
					width: 100%;
					max-width: 83.33333%;
				}
			}
		}
	}

	&_main-page {
		position: relative;
		margin-bottom: 1px;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: -1px;
			display: block;
			border-bottom: 1px solid $color-white;
		}
	}

	&__contacts {
		@media (max-width: 1024px) {
			margin-bottom: 25px;
		}
	}
}

.section-1 {
	@media (max-width: 768px) {
		min-height: 100vh;
	}
}
