.main-menu {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 99;
	display: none;
	padding: 110px 15px 30px;
	background-color: $color-endeavour;
	overflow-y: scroll;
	transform: translateX(100%);
	transition: transform 0.3s;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		height: 90px;
		z-index: 2;
		display: block;
		background-color: $color-endeavour;
	}

	&__nav-link {
		display: block;
		font-size: 18px;
		line-height: 24px;
		text-decoration: none;
		margin-bottom: 15px;
		color: $color-white;

		&:hover,
		&:focus,
		&:active {
			text-decoration: none;
			color: $color-white;
		}
	}

	&.active {
		transform: translateX(0);
	}

	@media (max-width: 1024px) {
		display: block;
	}
}
