.info-block-1 {
	display: block;
	text-decoration: none;
	color: $color-black;
	margin-bottom: 30px;

	&__image {
		height: 270px;
		margin-bottom: 35px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		margin-bottom: 22px;
		font-weight: 300;
		font-size: 36px;
		line-height: 36px;

		span {
			border-bottom: 1px solid rgba($color-black, 0.3);
			transition: border-bottom-color 0.3s;
		}
	}

	&__content {
		font-size: 18px;
		line-height: 30px;
	}

	&:hover {
		.info-block-1 {
			&__title {
				span {
					border-bottom-color: $color-black;
				}
			}
		}
	}
}
