    background-color: #fff; }
  .header:not(.header_index).header__open-menu .header__mobile-menu-btn.active:after, .header:not(.header_index).header__open-menu .header__mobile-menu-btn.active:before {
    background-color: #fff; }
  .header:not(.header_index).header__open-menu::after {
    opacity: 0; }
