    vertical-align: middle;
    margin: 0;
    border: none;
    padding: 0;
    outline: none;
    width: 32px;
    height: 22px;
    background-color: transparent; }
    .header__mobile-menu-btn:hover, .header__mobile-menu-btn:focus, .header__mobile-menu-btn:active {
      outline: none; }
    .header__mobile-menu-btn::before, .header__mobile-menu-btn::after {
      content: "";
      position: absolute;
      left: 50%;
      display: block;
      width: 32px;
      height: 4px;
      background-color: #000;
      transform-origin: center center;
      transition: top 0.3s, bottom 0.3s, transform 0.3s, background-color .3s ease-out; }
    .header__mobile-menu-btn::before {
      top: 0;
      transform: translateX(-50%); }
    .header__mobile-menu-btn::after {
      top: 100%;
      transform: translate(-50%, -100%); }
    .header__mobile-menu-btn span {
      position: absolute;
      left: 50%;
      top: 50%;
      display: block;
      width: 100%;
      height: 4px;
      background-color: #000;
      transform: translate(-50%, -50%);
      transition: opacity 0.3s, background-color .3s ease-out; }
    .header__mobile-menu-btn.active::before, .header__mobile-menu-btn.active::after {
      top: 50%; }
    .header__mobile-menu-btn.active::before {
      transform: translate(-50%, -50%) rotate(-45deg); }
    .header__mobile-menu-btn.active::after {
      transform: translate(-50%, -50%) rotate(45deg); }
    .header__mobile-menu-btn.active span {
      opacity: 0; }
    @media (max-width: 1024px) {
