.main-page-slider {
	.swiper-container {
		display: flex;
		align-items: center;
		overflow: visible;

		@media (max-width: 1024px) {
			overflow: hidden;
			height: 100vh;
		}

		.swiper-wrapper {
			@media (max-width: 1024px) {
				align-items: center;
			}
		}

		.swiper-slide {
			pointer-events: none;

			@media (max-width: 1024px) {
				height: auto;
			}

			&.swiper-slide-active {
				pointer-events: auto;
			}
		}
	}

	@media (max-width: 1024px) {
		height: 100vh;
	}

	&__slide {
		font-weight: 300;
		color: $color-white;
		opacity: 0;
		transition: opacity 0.6s;

		.swiper-slide-active & {
			opacity: 1;
		}
	}

	&__title {
		margin-bottom: 80px;

		@media (max-width: 1024px) {
			margin-bottom: 30px;
		}
	}

	&__counter {
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		margin-right: 20px;
		font-weight: 200;
		font-size: 24px;
		line-height: 36px;
		color: $color-white;

		/*@media (max-width: 1024px) {
			margin-right: 0;
		}*/

		@media (max-width: 768px) {
			display: none;
		}
	}

	&__arrow {
		position: relative;
		z-index: 2;
		flex: 0 0 22px;
		width: 22px;
		height: 41px;
		cursor: pointer;
		opacity: 0.8;
		transition: opacity 0.3s;

		&:hover {
			opacity: 1;
		}

		&:focus {
			outline: none;
		}

		&.swiper-button-disabled {
			opacity: 0;
			cursor: auto;
		}

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}

		@media (max-width: 768px) {
			display: none;
		}
	}
}
