.vertical-nav {
	position: fixed;
	right: 80px;
	top: 0;
	z-index: 100;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100vh;

	&__content {
		margin-top: 40px;
		margin-bottom: 40px;
	}

	&__item {
		display: block;
		margin: 0 0 40px;
		width: 50px;
		height: 40px;
		text-decoration: none;
		background-image: url("../images/navigation/line-white.png");
		background-position: center;
		background-repeat: repeat-y;
		cursor: pointer;

		&:last-of-type {
			margin-bottom: 0;
		}

		&_active {
			background-image: url("../images/navigation/cup.png");
			background-position: 66%;
			background-repeat: no-repeat;
		}
	}

	&__before,
	&__after {
		margin: 0 auto;
		width: 1px;
		flex: 1 1 20vh;
		background-image: url("../images/navigation/line-white.png");
		background-position: center;
		background-repeat: repeat-y;
	}

	@media (max-width: 1024px) {
		display: none;
	}
}
