.event-card {
	font-size: 18px;
	color: #fff;
	background-color: #000;
	margin-bottom: 85px;

	@media (max-width: 1024px) {
		margin-bottom: 35px;
	}

	&__wrapper {
		position: relative;
		overflow: hidden;
		padding-bottom: 100%;
		width: 100%;
	}

	&__content {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	&__date {
		position: relative;
		z-index: 2;
		display: flex;
		justify-content: space-between;
		height: 20%;
		align-items: center;
	}

	&__dateSquare {
		box-sizing: border-box;
		white-space: nowrap;
		background-color: #0055a0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50%;
		height: 100%;
		padding: 5px;
	}

	&__dividingLine {
		background-color: #fff;
		width: 1px;
		height: 18px;
		margin: 0 5px;
	}

	&__text {
		cursor: pointer;
		position: absolute;
		bottom: 5%;
		left: 5%;
		font-size: 24px;
	}

	&__title {
		font-size: 24px;
	}

	&__description {
		font-size: 18px;
	}

	&__dayCircle {
		position: absolute;
		top: 20%;
		right: 4%;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background: #0055a0;
		width: 40px;
		height: 40px;

		&_dayOff {
			background-color: #d6b05f;
		}
	}

	&__image {
		background: #eee no-repeat center center cover;
		object-fit: cover;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
	}
}
