.nav-item {
	display: inline-block;
	vertical-align: middle;

	&__link {
		display: block;
		border-bottom: 1px solid rgba($color-black, 0.5);
		margin: 9px 45px 9px 0;
		font-size: 18px;
		line-height: 24px;
		text-decoration: none;
		color: inherit;
		transition: color 0.2s, border-bottom-color 0.2s;

		&:hover {
			color: $color-endeavour;
			border-bottom-color: inherit;
		}

		&_sublink {
			cursor: pointer;
			border-bottom: 1px dashed rgba($color-black, 0.5);
		}

		&.active {
			border-color: $color-active-header-link;
			color: $color-endeavour;
		}
	}

	&.active,
	&_active {
		& > .nav-item__link {
			border-color: $color-active-header-link;
			color: $color-endeavour;
		}
	}

	.nav-list {
		display: none;
	}

	&.show-list {
		& > .nav-list {
			display: block;

			@include media-breakpoint-down(lg) {
				padding-left: 25px;
			}
		}
	}
}
