.cart {
	position: fixed;
	right: 39px;
	top: 55px;
	display: inline-block;
	vertical-align: middle;
	margin-left: auto;
	width: 32px;
	text-decoration: none;
	text-align: center;
	color: $color-white;

	&__image {
		margin-bottom: 12px;
		width: 32px;
		height: 27px;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}

		@media (max-width: 1024px) {
			flex: 0 0 32px;
			margin-bottom: 0;
			margin-left: 8px;
		}
	}

	&__count {
		font-size: 18px;
		line-height: 18px;
		text-align: center;
	}

	@media (max-width: 1024px) {
		position: relative;
		left: 0;
		top: 0;
		display: inline-flex;
		flex-flow: row-reverse nowrap;
		align-items: center;
		color: $color-black;

		.section_main-page &,
		&.active {
			color: $color-white;
		}
	}
}
