.discount {
	margin-bottom: 90px;
	font-weight: 300;
	font-size: 24px;
	line-height: 36px;

	&__image {
		margin-bottom: 28px;
		height: 280px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		span {
			border-bottom: 1px solid rgba($color-white, 0.3);
			transition: border-bottom-color 0.2s;
		}
	}

	&:hover {
		.discount {
			&__title {
				span {
					border-bottom-color: $color-white;
				}
			}
		}
	}

	@media (max-width: 1024px) {
		margin-bottom: 30px;
	}
}
