.nav-list {
	&_white {
		.nav-item {
			color: $color-white;

			&__link {
				border-bottom: 1px solid rgba($color-white, 0.5);

				&:hover {
					border-bottom-color: inherit;
					color: darken($color-white, 15%);
				}

				&_sublink {
					border-bottom: 1px dashed rgba(darken($color-white, 15%), 0.5);
				}
			}
		}
	}

	&_block {
		.nav-item {
			display: block;
			width: 100%;

			&__link {
				display: inline-block;
			}
		}
	}
}
