.main-page-video {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: -1;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 2;
		display: block;
		background-color: $color-black;
		opacity: 0.3;
	}

	video {
		width: 100%;
		height: 100%;
		object-fit: cover;

		@media (max-width: 768px) {
			display: none;
		}
	}

	&__image {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		display: none;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 768px) {
			display: block;
		}
	}
}
