.columns {
	position: relative;
	width: 100%;
	height: 100vh;

	@media (max-width: 1024px) {
		display: none;
	}

	&__title {
		position: absolute;
		left: 0;
		right: 0;
		top: 60px;
		z-index: 3;
		color: $color-endeavour;
	}

	&__small-text {
		font-weight: 300;
		font-size: 24px;
		line-height: 36px;
	}

	&__content {
		display: flex;
		justify-content: space-between;
		height: 100%;
	}
}

.beer-item {
	position: relative;
	z-index: 1;
	border-right: 1px solid #eee;
	width: 33.33%;
	height: 100%;

	&__background {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: -1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background-color: $color-white;
		transition: background-color 0.2s;

		&::before {
			content: "";
			position: absolute;
			top: 380px;
			z-index: 1;
			display: block;
			width: 100%;
			height: 250px;
			background-color: #0055a0;
			transition: opacity 0.2s;
		}
	}

	&__name {
		position: absolute;
		top: 495px;
		z-index: 2;
		color: $color-white;
		transform: rotate(-90deg);
	}

	&__beer-img {
		position: absolute;
		left: 50%;
		top: 50%;
		z-index: 3;
		width: 270px;
		height: 100%;
		transform: translate(-50%, -50%);

		img {
			position: relative;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&:hover {
		cursor: url("../images/5/cursor1.cur") 30 40, default;

		.beer-item {
			&__content {
				background-color: transparent;

				&::before {
					opacity: 0;
				}
			}
		}
	}
}
.columns__bgImg {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: no-repeat center / cover;
	z-index: 1;
}
.columns__whiteHeaderText {
	position: absolute;
	overflow: hidden;
	text-overflow: clip;
	color: #fff;
}
.columns__whiteHeaderText h2 {
	white-space: nowrap;
}
.columns__bgWhite {
	position: absolute;
	z-index: 2;
	width: 100%;
	height: 100%;
	top: 0;
	background-color: #fff;
}
.columns__blueHeaderText {
	position: absolute;
	overflow: hidden;
}
.columns__blueHeaderText h2 {
	white-space: nowrap;
	pointer-events: auto;
}
.columns__blueHeaderText h2 a {
	color: #0055A0;
	border-color: rgba(0, 85, 160, 0.5);
}
.columns__blueHeaderText h2 a:hover {
	border-color: #0055a0;
}
.columns__blueHeaderText p {
	font-size: 24px;
}
.columns__beerImg {
	position: absolute;
	z-index: 100;
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.columns__beerImg img {
	position: relative;
	top: 250px;
	width: 270px;
}
@media (max-width: 1499px) {
	.columns__beerImg img {
		top: 150px;
		width: 220px;
	}
}
@media (max-width: 1259px) {
	.columns__beerImg img {
		top: 200px;
		width: 200px;
	}
}
@media (max-width: 1119px) {
	.columns__beerImg img {
		top: 120px;
		width: 300px;
	}
}
@media (max-width: 767px) {
	.columns__beerImg img {
		top: 200px;
		width: 150px;
	}
}
.columns__blueRectWrapper {
	position: absolute;
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
@media (max-width: 1119px) {
	.columns__blueRectWrapper {
		display: none;
	}
}
.columns__blueRect {
	position: absolute;
	z-index: 50;
	background-color: #0055a0;
	width: 100%;
	top: 380px;
	height: 250px;
}
@media (max-width: 1499px) {
	.columns__blueRect {
		top: 250px;
		height: 220px;
	}
}
@media (max-width: 1259px) {
	.columns__blueRect {
		height: 200px;
		top: 290px;
	}
}
.columns__beerName {
	position: absolute;
	z-index: 100;
	color: white;
	top: 495px;
	-webkit-transform: rotate(-90deg);
	-ms-transform: rotate(-90deg);
	transform: rotate(-90deg);
}
@media (max-width: 1499px) {
	.columns__beerName {
		top: 350px;
	}
}
@media (max-width: 1259px) {
	.columns__beerName {
		top: 390px;
	}
}
@media (max-width: 1119px) {
	.columns__beerName {
		top: 400px;
		left: 250px;
	}
}
@media (max-width: 1003px) {
	.columns__beerName {
		left: 130px;
	}
}
@media (max-width: 767px) {
	.columns__beerName {
		top: 330px;
		left: 0;
	}
}
.cursorText {
	pointer-events: none;
	display: none;
	position: absolute;
	z-index: 1000000;
}
.cursorText p {
	color: #fff;
}
