.breadcrumbs {
	margin-bottom: 30px;

	&__item {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin-right: 30px;
		text-decoration: none;
		color: $color-endeavour;
		cursor: pointer;

		&::before {
			content: "";
			position: absolute;
			right: -15px;
			top: 50%;
			display: block;
			border-right: 1px solid $color-endeavour;
			border-bottom: 1px solid $color-endeavour;
			margin-top: -5px;
			width: 6px;
			height: 6px;
			transform: rotate(-45deg);
			transform-origin: right center;
		}

		&_last {
			margin-right: 0;
			color: $color-black;
			cursor: default;

			&::before {
				display: none;
			}
		}
	}
}
