.footer {
	padding-top: 80px;
	padding-bottom: 85px;
	font-weight: 300;
	color: $color-white;
	background-color: $color-endeavour;

	a {
		color: $color-white;
	}

	p {
		margin-top: 0;
	}

	&__social-link {
		display: inline-block;
		vertical-align: middle;
		margin-left: 25px;
		font-size: 20px;
	}

	&__list {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			margin-bottom: 15px;
			padding-left: 0;

			&:last-child {
				margin-bottom: 0;
			}

			a {
				border-bottom: 1px solid rgba($color-white, 0.3);
				text-decoration: none;

				&:hover {
					border-bottom-color: $color-white;
				}
			}
		}
	}

	@media (max-width: 768px) {
		[class^="col"] {
			margin-bottom: 30px;
		}
	}
}
