// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;
$color-endeavour: #0055a0;
$color-active-header-link: #b2cce2;

// FONTS
$font-garamond: 'EB Garamond';
$font-roboto: 'Roboto';
$font-myriad: 'Myriad Pro';

$spacer: 10px !default;
$spacers: () !default;
// stylelint-disable-next-line scss/dollar-variable-default
$spacers: map-merge(
		(
			0: 0,
			1: $spacer / 0.5,
			2: $spacer,
			3: ($spacer * 1.5),
			4: ($spacer * 2),
			5: ($spacer * 2.5),
			6: ($spacer * 3),
			7: ($spacer * 3.5),
			8: ($spacer * 4),
			9: ($spacer * 4.5),
			10: ($spacer * 5),
			11: ($spacer * 5.5),
			12: ($spacer * 6),
			13: ($spacer * 6.5),
			14: ($spacer * 7),
			15: ($spacer * 7.5),
			16: ($spacer * 8),
			17: ($spacer * 8.5),
			18: ($spacer * 9),
			19: ($spacer * 9.5),
			20: ($spacer * 10),
		),
		$spacers
);
