.menu {
	display: flex;
	flex-flow: column nowrap;
	height: 100%;
	align-items: flex-start;
	padding-right: 30px;

	&__title {
		display: inline-flex;
		flex: 0 0 auto;
		margin-bottom: 50px;
		font-family: $font-garamond;
		font-size: 85px;
		line-height: 80px;
		color: $color-endeavour;

		span {
			border-bottom: 1px solid rgba($color-endeavour, 0.27);
		}
	}

	&__content {
		width: 100%;
		flex: 0 1 100%;
	}
}
